import { Fragment, useContext, useEffect, useRef, useState } from "react";
import Dropzone from "react-dropzone";

import classes from "./addProject.module.css";
import Spinner from "../UI/spinner";
import Modal from "../UI/modal";
import { apiRootURL, tokenIndentation } from "../../apiConfigs";
import AuthContext from "../../store/authContexts";

const AddProject = () => {
  const [technologies, setTechnologies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [responseMessage, setresponseMessage] = useState("");

  const authCTX = useContext(AuthContext)

  const projectTitle = useRef();
  const client = useRef();
  const category = useRef();
  const link = useRef();
  const description = useRef();
  const fromDate = useRef();
  const toDate = useRef();
  const servicesRef = useRef([]);
  const technologiesRef = useRef([]);

  let mainImage;
  let projectImages;

  const services = [
    {
      name: "Design",
      value: "Design",
    },
    {
      name: "Development",
      value: "Development",
    },
    {
      name: "Deploy",
      value: "Deploy",
    },
    {
      name: "Support",
      value: "Support",
    },
  ];

  useEffect(() => {
    fetch(apiRootURL+"development/technologies")
      .then((res) => {
        return res.json();
      })
      .then((jsonRes) => {
        const fetchedTechs = [...jsonRes];
        return setTechnologies(() => {
          return [...fetchedTechs];
        });
      })
      .then(() => {
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
        setError("Error: Could not fetch Technologies");
        throw new Error("Error: Could not fetch Technologies");
      });
  }, []);

  const addProjectHandler = async (event) => {
    event.preventDefault();

    setLoading(true)
    const projectDetailObject = {
      title: projectTitle.current.value,
      clientName: client.current.value,
      category: category.current.value,
      link: link.current.value,
      description: description.current.value,
      from: fromDate.current.value,
      to: toDate.current.value,
      mainImage: mainImage,
      images: projectImages,
      services: servicesRef.current
        .filter((service) => {
          return service.checked;
        })
        .map((service) => {
          return service.defaultValue;
        }),
      technologies: technologiesRef.current
        .filter((technology) => {
          return technology.checked;
        })
        .map((technology) => {
          return technology.defaultValue;
        }),
    };

    const method = "POST";
    const URL =apiRootURL+"development/project?folder=projects&project=" + projectDetailObject.title.toUpperCase();
    const formData = new FormData();
    
    Object.entries(projectDetailObject).map(([key, value]) => {
      if (
        (key === "technologies" ||
          key === "services" ||
          key === "mainImage" ||
          key === "images") &&
        projectDetailObject[key]
      ) {
        return projectDetailObject[key].map((v) => {
          return formData.append(key, v);
        });
      } else {
        return formData.append(key, value);
      }
    });

    let errorExists = false;
    try {
        const res = await fetch(URL, { method: method, body: formData, headers : { Authorization : tokenIndentation+authCTX.token}})
        if (!res.ok) {
            errorExists = true;
        }
        const resJson = await res.json();
        if (errorExists) {
            const error = new Error("Error: " + resJson.message);
            throw error;
        }
        setresponseMessage(resJson.message);
        setLoading(false);
    } catch (err) {
        setLoading(false);
        setError(err.message);
    }
  };

  const closeModal = () => {
    setError("");
    setresponseMessage("");
  };

  const droppMain = (files) => {
    mainImage = files;
  };

  const droppImages = (files) => {
    projectImages = files;
  };

  let content =  (
    <div className={`${classes["add-project-page"]} bg-grey-neu`}>
      <form
        className={`${classes["add-project-form"]} neum-form`}
        onSubmit={addProjectHandler}
      >
        <h3 className={classes["form-title"]}>Add Project</h3>

        {/* project title */}
        <div className="form-input">
          <input
            ref={projectTitle}
            className="form-input-neu"
            type="text"
            placeholder="Project Title"
          />
        </div>

        <div className="form-row">
          {/* client name */}
          <div className="form-input-50">
            <input
              ref={client}
              className="form-input-neu"
              type="text"
              placeholder="Client Name"
            />
          </div>

          {/* Category */}
          <div className="form-input-50">
            <input
              ref={category}
              className="form-input-neu ml-auto"
              type="text"
              placeholder="Category"
            />
          </div>
        </div>

        {/* Link */}
        <div className="form-input">
          <input
            ref={link}
            className="form-input-neu"
            type="text"
            placeholder="Link"
          />
        </div>

        {/* Description */}
        <div className="form-input">
          <textarea
            ref={description}
            className="form-input-neu"
            type="text"
            placeholder="Description"
          />
        </div>

        {/* Date from */}
        <div className="form-input">
          <h5>From</h5>
          <input
            ref={fromDate}
            className="form-input-neu"
            type="date"
            placeholder="from"
          />
        </div>

        {/* Date to */}
        <div className="form-input">
          <h5>To</h5>
          <input
            ref={toDate}
            className="form-input-neu"
            type="date"
            placeholder="to"
          />
        </div>

        {/* Main Image */}
        <Dropzone multiple={false} accept="image/*" onDrop={droppMain}>
          {({ getRootProps, getInputProps }) => (
            <section className={classes["dropzone-container"]}>
              <div className="dropzone-area-neu" {...getRootProps()}>
                <input {...getInputProps()} />
                <p>Drop Main Image</p>
              </div>
              <aside>
                <ul className={classes["file-list"]}></ul>
              </aside>
            </section>
          )}
        </Dropzone>

        {/* Images */}
        <Dropzone onDrop={droppImages}>
          {({ getRootProps, getInputProps }) => (
            <section className={classes["dropzone-container"]}>
              <div className="dropzone-area-neu" {...getRootProps()}>
                <input {...getInputProps()} />
                <p>Drop Images</p>
              </div>
              <aside>
                <ul className={classes["file-list"]}></ul>
              </aside>
            </section>
          )}
        </Dropzone>

        {/* Services */}
        <h5>Services: </h5>
        <div className="form-input">
          {services.map((service, i) => {
            return (
              <div key={i} className="form-check-neu d-inline mx-1 w-sm">
                <input
                  ref={(el) => (servicesRef.current[i] = el)}
                  className="checkbox-neu"
                  type="checkbox"
                  value={service.value}
                  id="services"
                />
                <label
                  className="checkbox-label-neu"
                  htmlFor="flexCheckDefault"
                >
                  {service.name}
                </label>
              </div>
            );
          })}
        </div>

        {/* Technologies */}
        <h5>Technologies: </h5>
        <div className="form-input">
          {technologies.map((tech, i) => {
            return (
              <div key={tech._id} className="form-check-neu d-inline w-sm">
                <input
                  ref={(el) => (technologiesRef.current[i] = el)}
                  className="checkbox-neu"
                  placeholder={tech.name}
                  type="checkbox"
                  value={tech._id}
                  id="technologies"
                />
                <label
                  className="checkbox-label-neu"
                  htmlFor="flexCheckDefault"
                >
                  {tech.name}
                </label>
              </div>
            );
          })}
        </div>

        <div className="mt-3">
          <button className="neu" type="submit">
            ADD IT
          </button>
        </div>
      </form>
    </div>
  );

  return (
    <Fragment>
      {error !== "" && (
        <Modal error={true} onClose={closeModal}>
          {error}
        </Modal>
      )}
      {responseMessage !== "" && (
        <Modal error={false} onClose={closeModal}>
          {responseMessage}
        </Modal>
      )}
      {content}
      {loading && <Spinner />}

    </Fragment>
  );
};

export default AddProject;
